<template>
  <v-card class="mx-auto">
    <v-card-title>
      <v-row>
        <v-col>
          Vaccine inventory logs
        </v-col>
        <v-spacer />

        <v-col>
          <v-select
            label="Storage filter"
            v-model="storageFilter"
            :items="storages"
          />
        </v-col>

        <v-col>
          <v-select
            label="Manufacturer filter"
            v-model="mfrFilter"
            :items="mfrs"
          />
        </v-col>
        <v-col class="ml-2">
          <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search (Serial/Lot)"
          single-line
          hide-details
        ></v-text-field>
        </v-col>
      </v-row>
      
    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="filteredItems"
      :loading="isLoadingItems"
      :search="search"
      item-key="id"
    >
      <template v-slot:item.transaction_type="{item}">
        {{item.transaction_type === 'incoming' ? 'Incoming' : 'Outgoing'}}
      </template>
      <template v-slot:item.created_at="{item}">
        {{formatDate(item.created_at)}}
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import axios from 'axios'
import { format } from 'date-fns'

export default {
  name: 'CsmVaccineLogsTable',
  data: () => ({
    headers: [
      {
        text: 'Transaction',
        value: 'transaction_type',
        filterable: false
      },
      {
        text: 'Amount',
        value: 'amount',
        filterable: false
      },
      { text: 'Manufacturer', value: 'manufacturer', filterable: false },
      { text: 'Storage', value: 'storage_unit_name', filterable: false},
      { text: 'Serial', value: 'serial' },
      { text: 'Lot number', value: 'lot_no' },
      { text: 'Notes', value: 'comments', filterable: true },
      { text: 'Timestamp', value: 'created_at', filterable: false },
    ],
    alert: {
      isOpen: false,
      type: 'warning',
      message: 'Something went wrong. Please contact your IT.',
      color: 'warning',
    },
    items: [],
    storages: [],
    isLoadingItems: false,
    search: '',
    mfrFilter: 'All',
    storageFilter: 'Any'
  }),

  created () {
    this.fetchVaccineLogs()
    this.fetchStorageUnits()
  },

  computed: {
    mfrs () {
      const list = []

      this.items.forEach (item => {
        if (!list.includes(item.manufacturer)) list.push(item.manufacturer)
      })

      console.log(this.items)

      return ['All', ...list]
    },

    filteredItems () {
      let filtered = this.items
      if (this.mfrFilter !== 'All')
        filtered = filtered.filter(item => item.manufacturer === this.mfrFilter)
      else if (this.storageFilter !== 'Any')
        filtered = filtered.filter(item => item.storage_unit_name === this.storageFilter)
      
      return filtered
    }
  },

  methods: {
    async fetchStorageUnits () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/storage-units`, config)
        const storageNames = data.map(v => v.name)
        this.storages = ['Any', ...storageNames]
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login')
        }

        this.alert.isOpen = true
      }
    },

    async fetchVaccineLogs () {
      this.isLoadingItems = true

      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/vaccine-logs`, config)
        this.items = data

        this.isLoadingItems = false
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login')
        }

        this.alert.isOpen = true
      }
    },

    formatDate(date) {
      return format(new Date(date), 'PPpp')
    },
  }
}
</script>
